.filters {
  padding-top: 26px;

  &__form {
    display: flex;
    flex-wrap: wrap;
  }

  &__btn {
    width: 144px;
    height: 37px;
    border: solid 2px #fff;
    border-radius: 25px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    background-color: #9db1a5;
    color: #ffffff;
    align-self: end;

    &:hover,
    &:focus {
      color: #000000;
      background-color: #ffffff;
    }

    &:active {
      box-shadow: inset 0px 2px 1px 0 #696969;
      background-color: #ededed;
    }
  }
}
