.offers {
  position: relative;

  &__toggle-btns-list {
    display: flex;
    position: absolute;
    bottom: 62px;
    left: 0;
  }

  &__toggle-btns-item {
    width: 21px;
    height: 21px;
    margin-right: 8px;
  }

  &__toggle-btn {
    width: 100%;
    height: 100%;
    padding: 0;
    border-radius: 50%;
    border: 1px solid #ffffff;
    background-color: transparent;
    cursor: pointer;

    &:hover,
    &:focus {
      background-color: #9db1a5;
    }

    &--current {
      background-color: #ffffff;
      cursor: default;

      &:hover,
      &:focus {
        background-color: #ffffff;
      }
    }
  }
}
